import { useState, useRef } from "react";
import { useTheme } from "@mui/material/styles";
import Box from "@mui/material/Box";
import MobileStepper from "@mui/material/MobileStepper";
import Paper from "@mui/material/Paper";
import Button from "@mui/material/Button";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import SwipeableViews from "react-swipeable-views";
import NavBar from "../components/NavBar";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import { useNavigate } from "react-router-dom";
const images = [
  {
    label: "  ",
    imgPath: "assets/logbox/travel2.jpg",
  },
  {
    label: "   !",
    imgPath: "assets/logbox/travel3.jpg",
  },
  {
    label: "   ?",
    imgPath: "assets/logbox/memorize.jpg",
  },
  {
    label: "    ",
    imgPath: "assets/logbox/post.jpg",
  },
  {
    label: "  ,     ",
    imgPath: "assets/logbox/custom_collection.jpg",
  },
  {
    label: "  logbox       ",
    imgPath: "assets/logbox/share.jpg",
  },
  {
    label: "     ",
    imgPath: "assets/logbox/share2.jpg",
  },
  {
    label: "     ",
    imgPath: "assets/logbox/postBox.jpg",
  },
];

const style = {
  position: "absolute",

  top: "70%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "80%",
  borderRadius: "8px",
  boxShadow: 24,
  px: 0,
  pb: 5,
};

function LogBox() {
  const theme = useTheme();
  const [activeStep, setActiveStep] = useState(0);
  const maxSteps = images.length;
  const navigate = useNavigate();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  const state = 1;
  const CLIENT_ID = "1cdhp17WpXR_m9BDcOcE"; //  
  // const redirectURI = "http://localhost:3000/naver";
  const redirectURI = "https://i8a502.p.ssafy.io/naver";
  const naverLogin = `https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=${CLIENT_ID}&redirect_uri=${redirectURI}&state=${state}`;

  return (
    <div>
      <Box sx={{ width: "100vw", height: "100vh", flexGrow: 1 }}>
        <NavBar
          navText={"?"}
          leftChild={<ArrowBackIosNewIcon onClick={() => navigate(-1)} />}
        />
        {/* <TopBar></TopBar> */}
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          nextButton={
            <Button
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button
              size="small"
              onClick={handleBack}
              disabled={activeStep === 0}
            >
              {theme.direction === "rtl" ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
            </Button>
          }
        />
        <SwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
        >
          {images.map((step, index) => (
            <div key={step.label} style={{ margin: "1%" }}>
              <Box
                component="img"
                sx={{
                  height: "60vh",
                  display: "block",
                  overflow: "hidden",
                  width: "100%",
                  objectFit: "cover",
                  borderRadius: "10px",
                }}
                src={step.imgPath}
                alt={step.label}
              />
              {/* {activeStep < maxSteps - 1 ? null : (
                <Box
                  sx={{
                    height: "20%",
                    display: "block",
                    overflow: "hidden",
                    width: "80%",
                    objectFit: "cover",
                    borderRadius: "10px",
                  }}
                >
                  <Button>
                    <a href={naverLogin}>
                      <img
                        src={process.env.PUBLIC_URL + `assets/naverLogin.png`}
                        alt="naver"
                        style={{
                          height: "6vh",
                          display: "flex",
                          objectFit: "cover",
                        }}
                      ></img>
                    </a>
                  </Button>
                </Box>
              )} */}
            </div>
          ))}
        </SwipeableViews>

        <Paper
          square
          elevation={0}
          sx={{
            display: "flex",
            height: "10vh",
            pl: 2,
            bgcolor: "background.default",
            wordBreak: "keep-all",
            marginTop: "5%",
          }}
        >
          <div
            style={{
              fontSize: 25,
              display: "flex",
              margin: "auto",
              marginTop: "3%",
              textAlign: "center",
            }}
          >
            {images[activeStep].label}
          </div>
        </Paper>
        {activeStep < maxSteps - 1 &&
        sessionStorage.getItem("loginUser") ? null : (
          <a href={naverLogin}>
            <img
              src={process.env.PUBLIC_URL + `/assets/naverLogin.png`}
              alt="naver"
              style={{
                height: "6vh",
                display: "flex",
                margin: "auto",
                marginTop: "3%",
                objectFit: "cover",
              }}
            ></img>
          </a>
        )}
      </Box>
    </div>
  );
}

export default LogBox;
